@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,700;1,100;1,200;1,300;1,400&family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,700;1,200;1,300;1,400&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-size:10px;font-family:"Nunito Sans",sans-serif;color:#fff}body{overflow-x:hidden}button{cursor:pointer}

.nav-buffer{height:10vh}

.Nav__nav{width:100vw;height:10vh;display:flex;position:fixed;top:0;justify-content:center;align-items:center;background:radial-gradient(circle at center, #32333f 0, rgba(50,51,63,0.9) 66.66%);z-index:4;box-shadow:0px 15px 15px 0px rgba(0,0,0,0.3)}.Nav__nav a{font-size:1.8rem;margin:0px 20px;text-decoration:none;color:#fff}

.Hero__hero{width:100vw;height:90vh;background:#32333f;display:flex;align-items:center;flex-direction:column}.Hero__hero .hero__title{font-size:8rem;text-align:center;width:70%;font-family:"Montserrat",sans-serif;font-weight:300;margin:2% 0%}.Hero__hero .hero__subtitle{font-size:4rem;text-align:center;width:70%;font-family:"Montserrat",sans-serif;font-weight:200;margin:2% 0%}.Hero__hero .hero__glide-container{height:30%;width:100%;margin:2% 0%}.Hero__hero .hero__glide-container .scrolling__container{width:100%;height:100%;padding:5%}.Hero__hero .hero__glide-container .scrolling__container .container__card{width:100%;height:100%;background:#ffffff;border-radius:5px;display:flex;justify-content:center;align-items:center;border:1px solid black}.Hero__hero .hero__glide-container .scrolling__container img{width:80%}.Hero__hero .hero__buttons{display:flex;justify-content:center;align-items:center}.Hero__hero .hero__buttons button{width:100px;height:40px;border-radius:5px;border:none;background:transparent;font-size:1.6rem;margin:0% 10px}.Hero__hero .hero__buttons button:nth-child(1){color:#fff;background:#444;border:1px #fff solid}.Hero__hero .hero__buttons button:nth-child(2){background:#fff;color:#444;border:1px #444 solid}.Hero__hero .hero__buttons button a{text-decoration:none;font-size:1.6rem}


.GettingStarted__install{background:#32333f;width:100%;display:flex}@media only screen and (min-height: 800px){.GettingStarted__install{scroll-snap-align:start}}.GettingStarted__install .install__inner{padding:1% 2% 0% 2%;width:75%;display:flex;flex-direction:column}.GettingStarted__install .install__title{font-size:4rem;font-family:"Montserrat",sans-serif;margin-bottom:0.5%;width:40%;min-width:600px}.GettingStarted__install .install__subsubtitle{font-size:1.6rem;margin-bottom:0.4%;width:40%;min-width:600px}.GettingStarted__install .install__import,.GettingStarted__install .install__pre-reqs{font-size:1.6rem;margin-bottom:0.4%;width:40%;min-width:600px}.GettingStarted__install .code__area{width:40%;min-width:600px;margin-bottom:1.5%}.GettingStarted__install .code__area code,.GettingStarted__install .code__area span{font-size:1.6rem}.GettingStarted__install .glide-container{width:100%;height:12vh;margin-top:2%}.GettingStarted__install .glide-container img{width:80%}

.BasicSetup__basic-setup{width:100%;background:#1b1c23;display:flex;justify-content:center;flex-direction:column;min-height:90vh}@media only screen and (min-height: 800px){.BasicSetup__basic-setup{scroll-snap-align:start}}.BasicSetup__basic-setup .basic-setup__containers{width:75%;display:flex;flex-direction:column;align-items:center}.BasicSetup__basic-setup .basic-setup__containers .containers__left{width:60%;display:flex;justify-content:center;align-items:center;flex-direction:column;margin:1% 0%}.BasicSetup__basic-setup .basic-setup__containers .containers__left .left__title{font-size:3rem;margin-bottom:2%;text-align:center}.BasicSetup__basic-setup .basic-setup__containers .containers__left .left__subtitle{text-align:center;font-size:1.8rem;width:80%}.BasicSetup__basic-setup .basic-setup__containers .containers__right{margin:1% 0%;width:60%}.BasicSetup__basic-setup .basic-setup__containers .containers__right code,.BasicSetup__basic-setup .basic-setup__containers .containers__right pre{height:400px}.BasicSetup__basic-setup .glide-container{margin:1% 0%;width:100%;height:10vh}.BasicSetup__basic-setup .glide-container img{width:80%}

.display__tabs{height:8%;display:flex}.display__tabs button{font-size:2rem;width:80px;border:none;background:#282C34}.display__tabs button.active{background:whitesmoke;color:#282C34}.display__tabs button:focus{outline:none}.display__css-jsx{height:80%}.display__css-jsx pre,.display__css-jsx code,.display__css-jsx code span{height:100%;width:100%;font-size:1.6rem}

.DocsSideNav__nav{min-height:90vh;position:fixed;bottom:0;background:#3d3f4d;width:25%;min-width:250px;right:0;display:flex;flex-direction:column;padding:2%;transition:0.75s ease all;z-index:2}.DocsSideNav__nav button{text-decoration:none;margin:1% 0%;font-size:2rem;transition:all 0.3s ease;border:none;background:none}.DocsSideNav__nav button.active{color:#bd2dbd}.DocsSideNav__nav button:hover{color:navy}

.Mappers__mappers{width:100%;min-height:90vh;background:#32333f}.Mappers__mappers .mappers__container{width:75%;padding:1% 1.5%}.Mappers__mappers .mappers__container .container__title{font-size:4rem;font-family:"Montserrat",sans-serif;margin-bottom:0.5%;width:80%}.Mappers__mappers .mappers__container .container__subtitle{font-size:2.4rem;font-family:"Montserrat",sans-serif;margin-bottom:1%;width:80%}.Mappers__mappers .mappers__container .container__component-code{width:60%;margin-bottom:2%}.Mappers__mappers .mappers__container .container__component-code pre,.Mappers__mappers .mappers__container .container__component-code code{height:400px}.Mappers__mappers .glide-container{height:20vh;width:100%;margin:2% 0%}.Mappers__mappers .card{position:relative;margin:2%;width:90%;height:90%}.Mappers__mappers .front,.Mappers__mappers .back{width:100%;height:100%;overflow:hidden;-webkit-backface-visibility:hidden;backface-visibility:hidden;position:absolute;transition:transform .6s linear;box-shadow:10px 10px 10px rgba(0,0,0,0.2);border-radius:5px;border:1px solid #111111;background:#ffffff;display:flex;justify-content:center;align-items:center;font-size:16px;color:black}.Mappers__mappers .front{transform:perspective(600px) rotateY(0deg)}.Mappers__mappers .back{transform:perspective(600px) rotateY(180deg)}.Mappers__mappers .front img{width:80%}.Mappers__mappers .card:hover>.front{transform:perspective(600px) rotateY(-180deg)}.Mappers__mappers .card:hover>.back{transform:perspective(600px) rotateY(0deg)}

.Width__width{width:100%;min-height:90vh;background:#32333f}.Width__width .width__container{width:75%;padding:1% 1.5%}.Width__width .width__container .container__title{font-size:4rem;font-family:"Montserrat",sans-serif;margin-bottom:0.5%;width:80%}.Width__width .width__container .container__subtitle{font-size:2.4rem;font-family:"Montserrat",sans-serif;margin-bottom:1%;width:80%}.Width__width .width__container .container__subsubtitle{font-size:1.6rem;font-family:"Montserrat",sans-serif;margin-bottom:1%;width:80%}.Width__width .width__container .container__code{width:60%;margin-bottom:2%}.Width__width .width__container .container__code pre,.Width__width .width__container .container__code code{height:400px}.Width__width .width__container .glide-container{margin:2% 0%;width:100%;height:20vh}.Width__width .width__container .glide-container img{width:80%}

